@import url(https://fonts.googleapis.com/css?family=Karla:400,700);
.about {
    padding-top: 100px;
    padding-bottom: 130px;
    line-height: 25px;
    color: #616161;
}

.about__explain {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 100px auto;
    margin-bottom: 50px;
    width: 100%;
    max-width: 1200px;

}

.about-explain__column {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 270px;
    height: 270px;
    padding: 0 10px;
}

.expain__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 3em;
    background-color: #1FC1C7;
    width: 100px;
    height: 100px;
    border-radius: 50px;

}

.explain__title {
    margin: 15px 0 10px 0;
    font-size:1.7em;
    font-weight: 600;
}

.explain__content {
    text-align: center;
}

.about-profile {
    display: flex;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.about-profile__me {
    display: flex;
    flex: 1 1;
    flex-direction: column;
    align-items: center;
    padding: 0 50px;
}
.about-profile__skills {
    flex: 1 1;
}

.profile-me__img {
    width: 200px;
    height: 200px;
}

.profile-me__title {
    font-size:1.7em;
    font-weight: 600;
    margin: 20px auto;
}

.profile-me__content {
    text-align: center;
}

.profile-me__img img {
    width: 100%;
    height: 100%;
    border-radius: 100px;
}

.about-profile__skill {
    display: flex;
    height: 30px;
    justify-content: space-between;
    background-color: #EDEDED;
    margin-bottom: 10px;

}

.profile-skill__bar {
    width: 75%;
    background-color: #19A0A5;
}

.profile-skill__rate {
    margin-right: 10px;
}

.profile-skill__title {
    text-align: center;
    background-color: #20C1C7;
    color: white;
    height: 100%;
    font-weight: 600;
    width: 100px;
}
.header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header__underline {
    width: 100px;
    border: 2px solid #47494B;
    margin: 15px auto;
}

.header__title {
    color: #444649;
    font-weight: 600;
    font-size: 2.5em;
}

.header__subtitle {
    font-size: 1.5em;
    font-weight: 500;
}
.home {
    display: flex;
    width: 100%;
    height: 922px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: #1B242F;
}
.home-header__row{
    display: flex;
    flex-direction : column;
    justify-content: center;
    align-items: center;
}
.home-header__title{
    color: #fff;
}
.home-header__title-highlight {
    font-size: 60px;
    color : blueviolet;
}

.home-header__title{
    font-size: 50px;
}

.home-header__btn--over{
    border : 2px solid #fff;
    width: 300px; 
    height : 60px;
    margin-top: 20px;
    font-size: 30px;
    background-color:#1B242F;
    color: #fff;
    justify-content: center;
}

.home-header__btn--over:hover{
    border : 2px solid #04C2C9;
    background-color: #04C2C9;
    cursor: pointer; 
}

.portfolio {
    background-color: #F5F5F5;
    height: 100%;
    padding: 100px 0 130px 0
}

.portfolio__projects {
    width: 100%;
    max-width: 1280px;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    margin-top: 30px;
}

.portfolio__project {
    position: relative;
    width: 300px;
    height: 300px;
    overflow: hidden;
    margin: 10px;
}

.portfolio__project:hover img {
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}

.portfolio__project .project__details {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    background: rgba(0,0,0,.5);
    top: 15px;
    left: 15px;
    bottom: 15px;
    right: 15px;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s
}

.portfolio__project:hover .project__details {
    -webkit-transform: scaleY(1);
            transform: scaleY(1)
}

.portfolio__project .project__details h2 {
    color: #fff;
    font-size: 25px;
    cursor: default;
    margin-bottom: 5px;
}

.portfolio__project .project__details p {
    color: #fff;
    margin-bottom: 18px;
    font-size: 20px;
    cursor: default;
}
.portfolio__project .project__details .project-detail__more {
    border: 2px solid tomato;
    padding: 10px;
    cursor: pointer;
    color: white;
    transition: background .5s;
}

.portfolio__project .project__details .project-detail__more:hover {
    border: 2px solid tomato;
    background: tomato;    
}

.portfolio__project img {
    width: 100%;
    height: 100%;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s
}

.contact {
    height: 100vh;
    background: #252934;
    padding: 100px 0 130px 0;
    box-sizing: border-box;
    position: relative;
}

.contact-form__container {
    background: rgba(0,0,0,.3);
    width: 100%;
    max-width: 700px;
    margin: 0 auto;
    padding: 40px;
    box-sizing: border-box;
    box-shadow: 0 15px 25px rgba(0,0,0, .5);
    margin-top: 50px;
}

.contact-form__container form {
    display: flex;
}

.contact-form__container .contact-form__left {
    flex: 1.2 1;
    padding: 0 30px 0 10px;
    border-right: 1px solid #fff;
}

.contact-form__left .contact-form__box:nth-child(1) {
    margin-bottom: 30px;
}

.contact-form__container .contact-form__right {
    flex: 2 1;
    padding: 0 10px;

}

.contact-form__box {
    position: relative;

}

.contact-form__box label {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    padding: 7px 7px;
    font-size: 16px;
    pointer-events: none;
    color: #fff;
    font-weight: bold;
    transition: all .5s;
}

.contact-form__box input {
    width: 100%;
    padding: 7px 7px;
    box-sizing: border-box;
    color: #fff;
    font-size: 16px;
    border: none;
    outline: none;
    background: transparent;
    border-bottom: 1.5px solid #fff;
}

.contact-form__box textarea:valid ~ label,
.contact-form__box textarea:focus ~ label,
.contact-form__box input:valid ~ label,
.contact-form__box input:focus ~ label {
    color: #04C2C9;
    top: -20px;
    left: -7px;
    font-size: 13px;
}

.contact-form__box textarea {
    resize: none;
    width: 100%;
    box-sizing: border-box;
    height: 300px;
    border: none;
    outline: none;
    background: transparent;
    color: #fff;
    padding: 7px 7px;

    /* width: 100%;
    padding: 7px 7px; */
